.banner {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.banner > img {
  width: 100%;
}

[data-query="P"] {
  display: block;
}

[data-query="M"] {
  display: none;
}

.banner > section {
  position: absolute;
  text-align: right;
  left: 50%;
}

.mFrame.profession,
.mFrame.contact {
  background-color: var(--secondary);
}

.iconCard {
  text-align: center;
  padding: 0px 15px;
  width: 25%;
}

.iconCardTitle {
  color: var(--primary-main);
  margin-bottom: 8px;
}

.iconCardIcon {
  fill: var(--primary-main);
  width: 83px;
  height: 83px;
  margin-bottom: 24px;
}
.iconCardContent {
  text-align: left;
}

.horizentalCard {
  display: flex;
  align-items: center;
}
.horizentalCard:not(:last-child){
  margin-bottom: 32px;
}

.horizentalCardImg {
  width: 60%;
}

.horizentalCardContent {
  text-transform: uppercase;
  color: var(--primary-light);
  margin: 0 2rem;
  display: flex;
  align-items: center;
}

.seriesList > .horizentalCard:nth-child(2) {
  flex-direction: row-reverse;
}
.horizentalCardContent::before {
  content: "";
  margin-right: 22px;
  width: 44px;
  height: 44px;
  background-repeat: no-repeat;
  background-size: contain;
}
.seriesList > .horizentalCard:nth-child(1) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-diamond.svg");
}
.seriesList > .horizentalCard:nth-child(2) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-sun.svg");
}
.seriesList > .horizentalCard:nth-child(3) > .horizentalCardContent::before {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-thunder.svg");
}

.mFrame.cobrand {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/bg.png");
  background-position: center;
  background-repeat: no-repeat;
}
.list.cobrand > li {
  width: 50%;
  padding: 0px 15px;
}

.cobrandCard {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.cobrandCard::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
}

.cobrandCardImg {
  max-width: 100%;
}

.cobrandCardContent {
  position: absolute;
  color: var(--primary-main);
}
.list.commend > li {
  width: 25%;
  padding: 0px 15px;
  margin-bottom: 32px;
}
.commentCard {
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.commentCardImg {
  max-width: 100%;
}
.commentCardContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
}
.commentCardContent > footer {
  margin-top: 52px;
}

.mFrameBd.contact p {
  margin-bottom: 20px;
}
.formGroup {
  display: flex;
  align-items: center;
}
.formLabel {
  display: block;
  margin-bottom: 4px;
}
.formInput {
  border: 1px solid black;
  padding: 12px 16px 14px;
  display: block;
  margin-bottom: 16px;
  width: 100%;
}
.formCheckbox {
  width: 24px;
  height: 24px;
  margin-right: 4px;
}
.formCheckbox ~ label {
  display: inline;
  margin-bottom: 0;
}
.contactFormBtn {
  background-color: var(--primary-light);
  padding: 16px;
  color: #fff;
  width: 40%;
  margin: 0 auto;
  margin-top: 40px;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
  transition: 0.2s;
  &:hover{
    background-color: var(--primary-dark);
  }
}

@media (min-width: 768px) and (max-width: 979px) {
  .banner > section {
    right: 38px;
    left: auto;
  }
  .iconCard, .list.commend > li{
    width: 50%;
  }
  .seriesList > .horizentalCard:nth-child(2) {
    flex-direction: row;
  }
  .horizentalCardImg {
    width: 55%;
  }
}

@media (max-width: 767px) {
  [data-query="P"] {
    display: none;
  }
  [data-query="M"] {
    display: block;
  }
  .banner {
    & > section{
      right: 15px;
      left: auto;
      h4 {
        font-size: 16px;
      }
    }
  }
  .iconCard, .list.commend > li{
    width: 100%;
  }
  .horizentalCard{
    position: relative;
  }
  .horizentalCardContent{
    margin: 0;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 28px;
  }
  .horizentalCardImg {
    width: 100%;
  }
  .cobrandCardContent{
    font-size: 32px;
  }
  .list.cobrand > li {
    width: 100%;
    padding: 0px;
    margin-bottom: 16px;
  }
}